.App {
  text-align: center;
}

.Container {
  height: 950px;
  margin: 0 auto; /* 수평 가운데 정렬을 위한 margin 설정 */
}

.Button{
  width: 200px; /* 원하는 가로 크기 설정 */
  height: 60px; /* 원하는 세로 크기 설정 */
  margin-top: 10px; /* 버튼 위에 간격 추가 (선택사항) */
  border-radius: 10%;
  background:  #7AC7DB;
}

.Content {
  display: flex;
  align-items: center; /* 수직 가운데 정렬 */
  justify-content: center; /* 수평 가운데 정렬 */
}

.White-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  padding: 20px;
}

.Logo {
  height: 50px;
}

.Text-nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Text-nav-container h4 {
  margin-right: 10px; /* Join과 Login 사이의 간격을 조절합니다. */
}


.Page1 {
  height: 950px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Content p {
  text-align: left; /* 텍스트를 가운데 정렬 */
  font-size: 50px;;
}

.Content b {
  font-size: 20px;
}

.Content img {
  margin-left: 10px; /* 이미지와 텍스트 사이의 간격을 조절 (선택사항) */
  margin-left: 200px;
}

.Page2{
  padding: 50px;
  height: 950px;
  background:  #7AC7DB;
}